import { AbstractFilter } from './abstract_filter'

export class GeoDistance extends AbstractFilter {

    private name: string = 'geo_distance';
    private field: string;
    private distance: string;
    private point: any;

    constructor(field: string, lat: number, lon: number, distance: string) {
        super();
        this.field = field;
        this.point = { lat: lat, lon: lon };
        this.distance = distance;
    }

    public static make(field: string, lat: number, lon: number, distance: string): GeoDistance {
        return new GeoDistance(field, lat, lon, distance);
    }

    public toJSON() {
        let obj: any = {};
        obj[this.name] = {};
        obj[this.name]['distance'] = this.distance;
        obj[this.name][this.field] = this.point;

        return obj;
    }
}